/**
 * 
 * @remarks
 * 
 * xyy
 * yminymax(yyminymax)yscale-1
 * yminymax
 */
export class Bound {
    /**
     * 
     * @param {number} xmin - x
     * @param {number} ymin - y
     * @param {number} xmax - x
     * @param {number} ymax - y
     */
    constructor(xmin, ymin, xmax, ymax) {
        //+1 x-1
        this._xscale = 1;
        //+1 y-1
        this._yscale = 1;
        this._xmin = Math.min(xmin, xmax);
        this._ymin = Math.min(ymin, ymax);
        this._xmax = Math.max(xmin, xmax);
        this._ymax = Math.max(ymin, ymax);
        this._xscale = xmin <= xmax ? 1 : -1;
        this._yscale = ymin <= ymax ? 1 : -1;
    }
    /**
     * x
     * @return {number} x
     */
    get xmin() {
        return this._xmin;
    }
    /**
     * y
     * @return {number} y
     */
    get ymin() {
        return this._ymin;
    }
    /**
     * x
     * @return {number} x
     */
    get xmax() {
        return this._xmax;
    }
    /**
     * y
     * @return {number} y
     */
    get ymax() {
        return this._ymax;
    }
    /**
     * +1 x-1
     * @return {number} x
     */
    get xscale() {
        return this._xscale;
    }
    /**
     * +1 y-1
     * @return {number} y
     */
    get yscale() {
        return this._yscale;
    }
    /**
     * 
     * @return {number[]} [x,y]
     */
    getCenter() {
        return [(this._xmin + this._xmax) / 2, (this._ymin + this._ymax) / 2];
    }
    /**
     * 
     * @param {Bound} bound - 
     * @return {boolean} 
     */
    intersect(bound) {
        return (bound.xmax >= this._xmin) && (bound.xmin <= this._xmax) && (bound.ymax >= this._ymin) && (bound.ymin <= this._ymax);
    }
    /**
     * 
     * @param {number} s - 
     */
    scale(s) {
        this._xmin = this._xmin - (s - 1) * (this._xmax - this._xmin) / 2;
        this._xmax = this._xmax + (s - 1) * (this._xmax - this._xmin) / 2;
        this._ymin = this._ymin - (s - 1) * (this._ymax - this._ymin) / 2;
        this._ymax = this._ymax + (s - 1) * (this._ymax - this._ymin) / 2;
    }
    /**
     * 
     * @param {number} size - 
     */
    buffer(size) {
        this._xmin -= size;
        this._ymin -= size;
        this._xmax += size;
        this._ymax += size;
    }
}
