        this.strokeStyle = "#ffffff"; //#ff0000
        /**
         * 
         */
        this.outerFillStyle = "#ffffff"; //#ff0000
        /**
         * 
         */
        this.fontColor = "#ffffff";
        /**
         * 
         */
        this.fontFamily = "YaHei";
        /**
         * 
         */
        this.fontWeight = "Bold";
        /**
         * 
         */
        this.startColor = "#19caad";
        /**
         * 
         */
        this.endColor = "#f4606c";
